import React, { Fragment } from 'react';
import './App.css';

import { Animate } from './sqAni';
import {
  Vertex,
  projectOrthographic,
  projectPerspective,
  rotate3D
} from './sq3D';

const XyzPlanes = function(c, side, colors) {
  const d = side;

  this.vtcs = [
    new Vertex(c.x,   c.y,   c.z),
    new Vertex(c.x+d, c.y,   c.z),
    new Vertex(c.x+d, c.y+d, c.z),
    new Vertex(c.x,   c.y+d, c.z),
    new Vertex(c.x,   c.y+d, c.z+d),
    new Vertex(c.x,   c.y,   c.z+d),
    new Vertex(c.x+d, c.y,   c.z+d)
  ];

  this.faces = [
    [this.vtcs[0], this.vtcs[1], this.vtcs[2], this.vtcs[3]],
    [this.vtcs[0], this.vtcs[3], this.vtcs[4], this.vtcs[5]],
    [this.vtcs[0], this.vtcs[1], this.vtcs[6], this.vtcs[5]]
  ];

  this.colors = colors;
}

const Cube = function(c, side, colors) {
  // Generate the vertices
  const d = side/2;

  this.vtcs = [
    new Vertex(c.x-d, c.y-d, c.z+d),
    new Vertex(c.x-d, c.y-d, c.z-d),
    new Vertex(c.x+d, c.y-d, c.z-d),
    new Vertex(c.x+d, c.y-d, c.z+d),
    new Vertex(c.x+d, c.y+d, c.z+d),
    new Vertex(c.x+d, c.y+d, c.z-d),
    new Vertex(c.x-d, c.y+d, c.z-d),
    new Vertex(c.x-d, c.y+d, c.z+d)
  ];

  // Generate the faces
  this.faces = [
    [this.vtcs[0], this.vtcs[1], this.vtcs[2], this.vtcs[3]],
    [this.vtcs[3], this.vtcs[2], this.vtcs[5], this.vtcs[4]],
    [this.vtcs[4], this.vtcs[5], this.vtcs[6], this.vtcs[7]],
    [this.vtcs[7], this.vtcs[6], this.vtcs[1], this.vtcs[0]],
    [this.vtcs[7], this.vtcs[0], this.vtcs[3], this.vtcs[4]],
    [this.vtcs[1], this.vtcs[6], this.vtcs[5], this.vtcs[2]]
  ];

  this.colors = colors;
}

const Pyramid = function(c, side, colors) {
  const ds = side/2;
  const dh = side/3;

  this.vtcs = [
    new Vertex(c.x - ds, c.y - ds, c.z - dh),
    new Vertex(c.x + ds, c.y - ds, c.z - dh),
    new Vertex(c.x + ds, c.y + ds, c.z - dh),
    new Vertex(c.x - ds, c.y + ds, c.z - dh),
    new Vertex(c.x,      c.y,      c.z + 2*dh)
  ];

  this.faces = [
    [this.vtcs[0], this.vtcs[1], this.vtcs[2], this.vtcs[3]],
    [this.vtcs[0], this.vtcs[1], this.vtcs[4]],
    [this.vtcs[1], this.vtcs[2], this.vtcs[4]],
    [this.vtcs[2], this.vtcs[3], this.vtcs[4]],
    [this.vtcs[3], this.vtcs[0], this.vtcs[4]]
  ];

  this.colors = colors;
}

const Circle3D = function(c, r, colors, N=368/4) {  // N slices per quarter
  this.vtcs  = [];
  this.faces = [];
  const addVertex = (x, y, z) => {
    this.vtcs.push(new Vertex(c.x + x, c.y + y, c.z + z));
  }
  addVertex(0, 0, 0);
  let faces = [];
  for (let f=0; f < 4; f++) {
    faces.push([this.vtcs[0]]);
  }
  let v=1;
  for (let th=0, th2=Math.PI/2, dth=th2/N; th < th2; th += dth) {
    const r_cosTh = r*Math.cos(th);
    const r_sinTh = r*Math.sin(th);
    addVertex( r_cosTh,  r_sinTh, 0);
    addVertex(-r_cosTh,  r_sinTh, 0);
    addVertex(-r_cosTh, -r_sinTh, 0);
    addVertex( r_cosTh, -r_sinTh, 0);
    for (let j=0; j < 4; j++)
      faces[j].push(this.vtcs[v++]);
  }
  addVertex(0,  r, 0);  // add closing vertex for the quadrant I, II
  addVertex(0, -r, 0);  // for the quadrant III, IV
  for (let q=0; q < 4; q++) {
    const id = (q < 2) ? this.vtcs.length-2 : this.vtcs.length-1;
    faces[q].push(this.vtcs[id]);
    this.faces.push(faces[q]);
  }
  this.colors = colors;
}

const Cylinder = function(c, r, colors, N=72/4) {  // N slices per quarter
  this.vtcs  = [];
  this.faces = [];
  const addVertices = (x, y, z) => {
    this.vtcs.push(new Vertex(c.x + x, c.y + y, c.z + z + r/10));
    this.vtcs.push(new Vertex(c.x + x, c.y + y, c.z + z - r/10));
  }
  const addFaces = (i) => {
    for (let q=0; q < 4; q++) {
      this.faces.push([
        this.vtcs[2*q+8*(i-1)],
        this.vtcs[2*q+8*(i-1)+1],
        this.vtcs[2*q+8*i+1],
        this.vtcs[2*q+8*i]
      ]);
    }
  }
  for (let i=0, th=0, th2=Math.PI/2, dth=th2/N; i <= N; i++, th += dth) {
    const r_cosTh = r*Math.cos(th);
    const r_sinTh = r*Math.sin(th);
    addVertices( r_cosTh,  r_sinTh, 0); // 0,1   8,9   16,17
    addVertices(-r_cosTh,  r_sinTh, 0); // 2,3  10,11  18,19
    addVertices(-r_cosTh, -r_sinTh, 0); // 4,5  12,13  20,21
    addVertices( r_cosTh, -r_sinTh, 0); // 6,7  14,15  22,23
    if (i > 0) {
      addFaces(i);
    }
  }
  this.colors = colors;
}

// Vertex based rendering
// objects: a list of 3D objects
//   3D object: has {vtcs, faces, colors}
//     vtcs:  a list of vertices; vertice: a tuple (x, y, z)
//     faces: a list of faces;    face: an array vertices that form a face
const render3D = (objects, ctx, dx, dy, project, d=100) => {
  // Clear the previous frame
  ctx.clearRect(0, 0, 2*dx, 2*dy);
  let nVtcs=0;  // for profiling
  objects.forEach(object => {  // check the performance issue compared to the for loop
    object.faces.forEach((face, i) => {  // for each faces in the object, that is a array of face
      for (let i=0; i < face.length; i++) {  // loop through the array of vertices
        let P = project(face[i], d);
        let x =  P.x + dx;
        let y = -P.y + dy;
        if (i === 0) {
          ctx.beginPath();
          ctx.moveTo(x, y);
        } else {
          ctx.lineTo(x, y);
          // if (nVtcs % 10 === 0)
          // ctx.fillText(nVtcs, x, y);
        }
      }
      ctx.fillStyle = object.colors[i % object.colors.length];
      ctx.fill();
      ctx.closePath();
      ctx.stroke();
      nVtcs += face.length;  // for profiling
    })
  })
  return nVtcs;  // for profiling
}

// Code reuse by Render Props (-> objects props)
class Graphic3D extends React.Component {
  constructor(props) {
    super(props);
    this.paint = this.paint.bind(this);
  }

  componentDidMount() {
    this.paint();
  }

  componentDidUpdate() {
    this.paint();
  }

  paint() {
    // ############ Profiling BEGIN
    const t0 = performance.now();
    // ############ Profiling END
    const { width, height, data } = this.props;
    const ctx = this.refs.canvas.getContext("2d");
    const W = width / 2;
    const H = height / 2;

    //ctx.strokeStyle = 'rgba(0, 0, 0, 0.9)';
    ctx.lineWidth   = 1;
    ctx.globalAlpha = 0.7;

    const ctr = new Vertex(0, 1.2*H, 0);  // ctr.y should not be 0
    const bPerspective = (this.props.project === projectPerspective);
    const objects = this.props.objects(ctr, H);  // kind of Render Props

    const rad   = Math.PI*data/180;
    let dZoom = 80+40*Math.sin(rad);
    if (bPerspective) {  // In Perspective Projection, zooming effect is dependent on the object size units
      dZoom *= width/250;
    }
    let radTh = 0;
    let radPh = 0;
    if (data < 360*2) {  // first two cycles
      radTh = rad;
      radPh = rad;
    }
    else if (data < 360*3) {
      radPh = rad;
    }
    else {
      radTh = rad;
    }
    rotate3D(objects, ctr, -radTh, radPh);
    const nVtcs = render3D(objects, ctx, W, H, this.props.project, dZoom);

    // ############ Profiling BEGIN
    const t1 = performance.now();
    let prof = this.props.interval ? `${Math.round(1000/this.props.interval)}fps,` : '';
    prof += `${nVtcs}vtcs,` +
            `${(t1-t0).toFixed(2)}ms,` +
            `${Math.round(dZoom)}%,` +
            `${(data % 360).toFixed(1)}Â°`;
    ctx.font         = 'bold 8pt Courier New';
    ctx.textAlign    = 'center';
    ctx.textBaseline = 'top';
    ctx.fillStyle    = 'rgba(127,0,0,1)';
    ctx.fillText(bPerspective ?
      "< Perspective Projection >" :
      "- Orthographic Projection -", W, 0);
    ctx.textBaseline = 'bottom';
    ctx.fillStyle    = 'rgba(0,0,0,1)';
    ctx.fillText(prof, W, height);
    // ############ Profiling END
  }

  render() {
    const { width, height } = this.props;
    return (
      <canvas ref="canvas" width={width} height={height} />
    );
  }
}

