// Arrow functions are designed to be "throwaway" functions,
// and so cannot be used to define new types.
export const Vertex = function(x, y, z) {
  //this.x = parseFloat(x);
  //this.y = parseFloat(y);
  //this.z = parseFloat(z);
  this.x = x;
  this.y = y;
  this.z = z;
}

export const Vertex2D = function(x, y) {
  //this.x = parseFloat(x);
  //this.y = parseFloat(y);
  this.x = x;
  this.y = y;
}

// Orthographic projection
// d is a zoom factor, if d = 200, zoom in 200%
export const projectOrthographic = (M, d=100) => {
  const r = d/100;
  return (r === 1) ? new Vertex2D(  M.x,   M.z) :
                     new Vertex2D(r*M.x, r*M.z);
}

// Perspective view
// d: projection plane's distance from the camera
// As d becomes larger, the object view becomes also larger.
export const projectPerspective = (M, d=100) => {
  const r = d/M.y;   // y-axis defined as the opposite direction of the view
  return new Vertex2D(r*M.x, r*M.z);
}

export const rotate3D = (objects, ctr, theta, phi) => {
  const cosTh = Math.cos(theta);
  const sinTh = Math.sin(theta);
  const cosPh = Math.cos(phi);
  const sinPh = Math.sin(phi);

  objects.forEach(obj => {
    obj.vtcs.forEach(v => {
      // orient to the center
      const x = v.x - ctr.x;
      const y = v.y - ctr.y;
      const z = v.z - ctr.z;
      // [ cos(th), -sin(th)cos(ph),  sin(th)sin(ph) ]
      // [ sin(th),  cos(th)cos(ph), -cos(th)sin(ph) ]
      // [       0,         sin(ph),         cos(ph) ]
      v.x = ctr.x + cosTh*x - sinTh*cosPh*y + sinTh*sinPh*z;
      v.y = ctr.y + sinTh*x + cosTh*cosPh*y - cosTh*sinPh*z;
      v.z = ctr.z +                 sinPh*y +       cosPh*z;
    })
  })
}
